<script>
export default {
  data() {
    return {

      list: []
    };
  },

   computed: {
    optionHover() {
      return {
           step: 0.5, // 数值越大速度滚动越快
				   limitMoveNum: 3, // 启动滚动的数据量
				   hoverStop: true, // 是否开启鼠标悬停
				   direction: 1, // 0向下 1向上 2向左 3向右
				   openWatch: true, // 开启数据实时监控刷新dom
				   singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
				   singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
				   waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
      }
    }
  },

  mounted(){
    
    //window.setDataTunnel = this.setData;

    this.setData(window.parent.getTimelyData());
    setInterval(() => {
      this.setData(window.parent.getTimelyData());
}, 300000);

  },

 methods: {
    newShow(e){
      let data=e.target.dataset;
      console.log(data.index,data.name,data.fans,data.icon,data.tweets,data.content);
      window.parent.showDetails(data.content, '作者：'+data.name, data.fans, data.icon);
    },

    setData(listData) {

      for(let i=0;i<listData.data.length;i++)
     {if(listData.data[i].img=='') listData.data[i].img='https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fpic2.zhimg.com%2Fv2-ce85535f8f65e9d9de77e7cdd5ac10fa_1440w.jpg%3Fsource%3D172ae18b&refer=http%3A%2F%2Fpic2.zhimg.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1664902715&t=60b6a9d20e4cbc2b1adf52616684ea94';}
      
      this.list = listData.data;

      //listData.forEach((param, index) => {
        //this.list.push(param); });
      // 2.以上执行过程是遍历listData，将其每个元素push入组件内的youngList中，不可直接写成this.youngList = listData
    },

//getData函数已弃用
    async getData(){
      //登录
      let res = await this.axios.get("https://dt-test.xunku.org/core.action?action=portal.AppLogin&userName=csdlzy&password=9ff409ca93982aef16165de9cfe01812&vcode&platformCode=ca6887c8ed71c91a3a950c3ca50f96cf&ecCode=null&columnName=Name&sessdionCode")//接口地址
      if(res.data.statusCode=='SUCESS'){
        var data_token=res.data.token //获取请求头

      //获取实时数据
          this.axios.get('https://dt-test.xunku.org/core.action?action=subjectMonitor.SubjectMonitor.realTimeData',{
            headers:{'x-auth-token':data_token}
          }).then((res2)=>{
            //console.log(res2.data.data);
